.popular {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    height: 90vh; 
}

.popular h1 {
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}

.popular hr {
    width: 200px;
    height: 6px;
    border: 3px solid #171717;
    border-radius: 10px;
    background: #252525;
}


.popular-item {
    margin-top: 50px;
    display: flex;
    gap: 30px;
} 

@media(max-width: 1280px){
    .popular{
        height: 70vh;
        margin-top: 100px;
    }

    .popular h1 {
        font-size: 2.5rem;
        margin-top: 50px;
    }
    .popular hr {
        width: 160px;
        height: 4px ;
    }
    .popular-item{
        gap: 20px;
        margin-top: 30px;
    }
}

@media(max-width: 1024px){
    .popular{
        height: 60vh;
    }

    .popular h1 {
        font-size: 1.9rem;
        margin-top: 50px;
    }
    .popular hr {
        width: 120px;
        height: 3px ;
    }
    .popular-item{
        gap: 15px;
        margin-top: 20px;
    }
}

@media(max-width: 800px){
    .popular{
        height: 50vh;
        gap: 6px;
    }

    .popular h1 {
        font-size: 1.25rem;
        margin-top: 50px;
    }
    .popular hr {
        width: 100px;
    }
    .popular-item{
        gap: 5px;
    }
}

@media(max-width: 500px){
    .popular{
        height: 70vh;
        gap: 6px;
    }

    .popular h1 {
        font-size: 1.25rem;
        margin-top: 50px;
    }
    .popular hr {
        width: 100px;
    }
    .popular-item{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }
}