.navbar{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
    align-items: center;
}

.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}

.nav-logo p {
    color: #171717;
    font-size: 2.4rem;
    font-weight: 600;
}

.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    font-size: 1.2rem;
    font-weight: 500;
}

.nav-menu li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}

.nav-menu .nav-link{
    color: #515151;
}
.nav-menu hr {
    border: none;
    width: 90%;
    height: 3px;
    border-radius: 10px;
    background: #FF4141;
    margin-top: -5px;
}

.nav-login-cart {
    display: flex;
    align-items: center;
    gap: 45px;
}

.nav-login-cart button {
    width: 160px;
    height: 60px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 1.25rem;
    font-weight: 500;
    background: white;
    cursor: pointer;
}

.nav-login-cart button:active {
    background: #f3f3f3;
}

.nav-cart-count {
    width: 22px;
    height: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;
}

.nav-dropdown{
    display: none;
}

@media(max-width: 1280px){
    .navbar{
        padding: 12px 50px;
    }
    .nav-logo img {
        width: 40px; 
    }
    .nav-logo p {
        font-size: 25px;
    }
    .nav-menu{
        gap: 30px;
        font-size: 1rem;
    }
    .nav-login-cart {
        gap: 30px;
    }
    .nav-login-cart button {
        width: 120px;
        height: 45px;
        font-size: 1rem;

    }
    .nav-cart-count {
        margin-top: -45px;
        margin-left: -40px;
        font-size: 12px;
    }
}

@media(max-width: 1024px){
    .navbar{
        padding: 12px 30px;
    }
    .nav-logo img {
        width: 35px; 
    }
    .nav-menu{
        gap: 25px;
        font-size: 0.9rem;
    }
    .nav-login-cart {
        gap: 20px;
    }
    .nav-login-cart img {
        width: 30px;
    }
    .nav-login-cart button {
        width: 80px;
        height: 35px;
        font-size: 0.9rem;

    }
    .nav-cart-count {
        width: 18px;
        height: 18px;
        margin-top: -40px;
        margin-left: -30px;
    }
}

@media(max-width: 800px){
    .navbar{
        padding: 10px 0;
    }
    .nav-dropdown{
        display: block;
        width: 30px;
        height: 30px;
        rotate: -90deg;
        transition: 0.5s;   
    }
    .nav-menu{
        display: none;
        height: 80px;
        padding: 0 50px;
        position: absolute;
        background-color: #fff;
        justify-content: center;
        top: 50px;
        border-radius: 10px;
    }
    .nav-menu-visible {
        display: flex;
    }
    .nav-dropdown.open {
        transform: rotate(90deg);
    }
}

@media(max-width: 500px){
    .navbar{
        padding: 8px 0;
        gap: 0;
    }
    .nav-logo {
        transform: scale(0.8);
    }
    .nav-menu {
        height: 70px;
    }
    .nav-login-cart {
        transform: scale(0.8);
    }
}


